import { Logger } from "../Logger";
import { makeAsyncEval } from "../misc";
import { before } from "../patcher";

let ws: WebSocket | null;
let patched = false;
export function startDebugWs() {
    if (ws) return;
    ws = new WebSocket("ws://localhost:3000");

    const logger = new Logger("DebugWS");
    logger.info("Connecting to debug ws");

    ws.addEventListener("open", () => logger.info("Connected with debug websocket"));
    ws.addEventListener("error", e => logger.error((e as ErrorEvent).message));
    ws.addEventListener("message", async message => {
        try {
            const { data } = message;
            const parsed = JSON.parse(data);
            switch (parsed.type) {
                case "COMMAND":
                    if (parsed.message.includes("await")) {
                        console.log(await eval(makeAsyncEval(parsed.message)));
                    } else {
                        console.log(eval(parsed.message));
                    }
                    break;
                case "AUTOCOMPLETE":
                    if (parsed.message.match(/[\w\d.]+/)) {
                        if (parsed.message.includes(".")) {
                            const parts = parsed.message.split(".");
                            const context = parts.slice(0, parts.length - 1);
                            let value: unknown;
                            try {
                                value = eval(context.join("."));
                            } catch (e) {
                                break;
                            }
                            if (typeof value === "object" && !Array.isArray(value) && value) {
                                const keys = Object.keys(value);
                                ws?.send?.(JSON.stringify({
                                    type: "AUTOCOMPLETE",
                                    line: parsed.message,
                                    completions: keys.filter(k => k.startsWith(parts[parts.length])).map(k => [...context, k].join("."))
                                }));
                                break;
                            }
                        }
                    }
                    ws?.send?.(JSON.stringify({
                        type: "AUTOCOMPLETE",
                        line: parsed.message,
                        completions: []
                    }));
                    break;
            }
        } catch (e) {
            logger.error(e as Error | string);
        }
    });
    ws.addEventListener("close", () => {
        logger.info("Disconnected from debug websocket, reconnecting in 3 seconds");
        ws = null;
        setTimeout(startDebugWs, 3000);
    });

    if (!patched) {
        before(globalThis, "nativeLoggingHook", (_, message: string, level: number) => {
            if (ws?.readyState === WebSocket.OPEN) {
                ws.send(JSON.stringify({
                    type: "MESSAGE", level, message
                }));
            }
        });
        patched = true;
    }
}
